<template>
  <div class="flex container h-full w-full">
    <SideNav />
    
    <div class="flex-1 flex flex-col h-full">
      <!-- Header -->
      <div class="px-4 py-3 border-b border-lighter flex items-center">
        <router-link :to="{ name: 'conversations' }" class="mr-4">
          <i class="fas fa-arrow-left text-xl text-blue"></i>
        </router-link>
        
        <div v-if="conversation" class="flex items-center">
          <img :src="conversation.otherUser.avatar || 'default_profile.png'" 
               class="w-10 h-10 rounded-full mr-3" />
          <div>
            <p class="font-bold">{{ conversation.otherUser.username }}</p>
            <p class="text-sm text-dark">@{{ conversation.otherUser.id }}</p>
          </div>
        </div>
      </div>

      <!-- Messages -->
      <div class="flex-1 overflow-y-auto p-4" ref="messageContainer">
        <div v-if="loading" class="flex justify-center">
          <Loader :loading="loading" />
        </div>

        <template v-else>
          <div v-for="message in messages" :key="message.id" 
               class="mb-4 flex"
               :class="{'justify-end': message.senderId === currentUserId}">
            <div class="max-w-3/4 rounded-lg px-4 py-2"
                 :class="message.senderId === currentUserId ? 
                        'bg-blue text-white' : 'bg-lighter'">
              <p v-linkify>{{ message.content }}</p>
              <p class="text-xs mt-1 text-gray-500">
                {{ $filters.timeago(message.createdAt) }}
              </p>
            </div>
          </div>
        </template>
      </div>

      <!-- Message Input -->
      <div class="border-t border-lighter px-4 py-3">
        <form @submit.prevent="sendMessage" class="flex">
          <input v-model="newMessage" 
                 type="text"
                 placeholder="Type a message..."
                 class="flex-1 rounded-full border border-lighter px-4 py-2 mr-2" />
          <button type="submit" 
                  class="bg-blue text-white rounded-full px-6 py-2">
            Send
          </button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import SideNav from '../components/SideNav.vue';
import Loader from '../components/Loader.vue';

export default {
  name: 'Conversation',
  
  components: {
    SideNav,
    Loader
  },

  data() {
    return {
      loading: true,
      conversation: null,
      messages: [],
      newMessage: '',
      messagesCursor: null
    };
  },

  computed: {
    ...mapState({
      currentUserId: state => state.auth.user.id
    }),
    
    chatId() {
      return this.$route.params.chatId;
    }
  },

  methods: {
    async loadConversation() {
      try {
        this.conversation = await this.$store.dispatch('getConversation', {
          conversationId: this.chatId
        });
      } catch (error) {
        console.error('Error loading conversation:', error);
      }
    },

    async loadMessages() {
      try {
        const { messages, cursor } = await this.$store.dispatch('getMessages', {
          conversationId: this.chatId,
          cursor: this.messagesCursor,
          limit: 50
        });

        this.messages = [...messages, ...this.messages];
        this.messagesCursor = cursor;
        this.loading = false;
      } catch (error) {
        console.error('Error loading messages:', error);
        this.loading = false;
      }
    },

    async sendMessage() {
      if (!this.newMessage.trim()) return;

      try {
        const message = await this.$store.dispatch('sendMessage', {
          conversationId: this.chatId,
          content: this.newMessage
        });

        this.messages.push(message);
        this.newMessage = '';
        this.scrollToBottom();
      } catch (error) {
        console.error('Error sending message:', error);
      }
    },

    scrollToBottom() {
      this.$nextTick(() => {
        const container = this.$refs.messageContainer;
        container.scrollTop = container.scrollHeight;
      });
    }
  },

  async created() {
    await Promise.all([
      this.loadConversation(),
      this.loadMessages()
    ]);
    this.scrollToBottom();
  }
};
</script>