<template>
  <div id="app" class="flex container h-full w-full">
    <div class="flex container h-full w-full">
      <SideNav />

      <div class="w-full sm:w-1/3 md:w-1/2 lg:w-1/2 h-full overflow-y-scroll no-scrollbar hidden md:block">
        <div class="px-5 py-3 border-b border-lighter flex">
          <h1 class="text-xl font-bold flex-1">Conversations</h1>
          <i class="fas fa-cog text-xl text-blue cursor-pointer"></i>
          <i @click="newMessage()" class="fas fa-plus-circle ml-3 text-xl text-blue cursor-pointer"></i>
        </div>

        <!-- conversations -->
        <Loader :loading="loading" />
        <div v-if="!loading">
          <div v-if="conversations.length === 0" class="flex flex-col items-center justify-center w-full pt-10">
            <div class="w-1/2 flex flex-col items-center justify-center">
              <p class="font-bold text-lg">No conversations yet</p>
              <p class="text-sm text-dark">
                Wait until someone starts a conversation here.
              </p>
            </div>
          </div>
          <div v-for="conversation in conversations" :key="conversation.id">
            <div class="cursor-pointer">
              <div class="w-full p-2 pt-1 pb-1 md:p-4 md:pt-2 md:pb-2 border-b hover:bg-lightest flex"
                @click="selectConversation(conversation)">
                <div class="flex-none mr-2 md:mr-4 pt-1">
                  <a :href="`#/${conversation.otherUser.username}`">
                    <img :src="`${conversation.otherUser.avatar || 'default_profile.png'}`"
                      class="h-12 w-12 rounded-full flex-none" />
                  </a>
                </div>
                <div class="w-full truncate">
                  <div class="flex items-center w-full">
                    <p class="font-semibold">{{ conversation.otherUser.username }}</p>
                    <p class="hidden md:block text-sm text-dark ml-2 truncate">
                      @{{ conversation.otherUser.id }}
                    </p>
                    <p class="text-sm text-dark ml-auto">
                      {{ $filters.timeago(conversation.lastModified) }}
                    </p>
                  </div>
                  <p class="pb-2 truncate" v-linkify>{{ conversation.lastMessage }}</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="w-full border-l border-r border-lighter h-screen flex items-center justify-center">
        <div class="text-center">
          <h2 class="text-2xl font-bold mb-4">Select a conversation</h2>
          <p class="text-gray-600">Choose a conversation from the list to start chatting</p>
        </div>
      </div>

      <NewMessageOverlay
        v-if="showNewMessageModal"
        :showNewMessageModal.sync="showNewMessageModal"
        @selected="selected"
      />
    </div>
  </div>
</template>

<script>
import SideNav from "../components/SideNav.vue";
import Loader from "../components/Loader.vue";
import NewMessageOverlay from "../components/NewMessageOverlay.vue";
import { mapActions } from "vuex";

export default {
  name: "Conversations",
  components: {
    SideNav,
    Loader,
    NewMessageOverlay,
  },
  data() {
    return {
      loading: true,
      showNewMessageModal: false,
      conversations: [],
      conversationsCursor: "",
      profile: {}
    };
  },
  methods: {
    ...mapActions("warpnet", [
      "createConversation", 
      "deleteConversation",
      "getConversations",
    ]),
    newMessage() {
      this.showNewMessageModal = true;
    },
    async selectConversation(conversation) {
      this.$router.push({
        name: 'Messages',
        params: {
          chatId: conversation.id,
        },
      });
    },
    async selected(user) {
      if (!user.id) {
        return;
      }
      this.showNewMessageModal = false;

      // Check if conversation already exists
      const existingConversation = this.conversations.find(
        conv => conv.otherUser.id === user.id
      );

      if (existingConversation) {
        await this.selectConversation(existingConversation);
      } else {
        // Create new conversation
        const newConversation = await this.createConversation({
          otherUserId: user.id
        });
        
        if (newConversation) {
          await this.selectConversation(newConversation);
        }
      }
    },
    async loadConversations() {
      try {
        const { conversations, cursor } = await this.getConversations({
          limit: 20,
          cursor: this.conversationsCursor
        });
        
        this.conversations = [...this.conversations, ...conversations];
        this.conversationsCursor = cursor;
        this.loading = false;
      } catch (error) {
        console.error("Error loading conversations:", error);
        this.loading = false;
      }
    }
  },
  async created() {
    await this.loadConversations();
  }
};
</script>
